<template>
<div class="container">
  <div class="passportPreviewDiv">
    <div class="passportPreviewContainer" id="capture">
      <!-- Loop through 8 times -->
      <preview
        v-for="index in image_count_per_preview_container"
        :key="index"
        :width="x_wd_passport_preview"
        :height="y_wd_passport_preview"
        :image="result.image"
        :coordinates="result.coordinates"
        class="eachPassportImage"
      />
    </div>
  </div>

  <passport-buttons @download_preview_canvas="download_preview_canvas" />
</div>
</template>
<script>
import { Preview } from "vue-advanced-cropper";
import html2canvas from "html2canvas";
import passportButtons from "./passport-control-buttons.vue";
export default {
  data() {
    var scale_wd_passport_preview = 2;

    return {
      image_count_per_preview_container: 8,
      x_wd_passport_preview: 35 * scale_wd_passport_preview,
      y_wd_passport_preview: 45 * scale_wd_passport_preview,
      result: {
        coordinates: null,
        image: null,
      },
    };
  },
  name: "passport-preview",
  components: {
    Preview,
    passportButtons,
  },
  methods: {
    onChange({ coordinates, image }) {
      this.result = {
        coordinates,
        image,
      };
    },

    download_preview_canvas() {
      html2canvas(document.querySelector("#capture"), {
        // allowTaint: true,
        useCORS: true,
        height: this.y_wd_passport_preview * 2 + 10 + 20,
        width: this.x_wd_passport_preview * 4 + 5 * 3 + 5 * 4,
        // Quality = 10 means witdh*10, height*10
        scale: 10,
      })
        .then((canvas) => {
          var passphoto_canvas = document.body.appendChild(canvas);
          passphoto_canvas;
          // hide Canvas
          passphoto_canvas.style.display = "none";
          canvas.toDataURL("image/png");

          var anchor = document.createElement("a");
          anchor.href = canvas.toDataURL("image/png");
          anchor.download = "PassportImage.png";
          anchor.click();
        })
        .then(() => {});
    },
  },
};
</script>
<style scoped>
.passportPreviewDiv {
  width: 160px;
  height: 110px;
  margin-top: 10px;
  margin-bottom: 100px;
}
.passportPreviewContainer {
  padding-left: 10px;
  padding-right: 5px;
  padding-top: 10px;
  padding-bottom: 10px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 10px 5px;
  grid-template-areas:
    ". . . ."
    ". . . .";
}
